module.exports = "\n:host {\n  display: flex;\n  position: relative;\n  font-family: Roboto, sans-serif;\n}\n\n.time-series {\n  width: 100%; \n  display: flex;\n  flex-direction: column;\n}\n\n.time-series-channel {\n  display: flex;\n  flex-direction: column;\n  width: 100%; \n  position: relative;\n  height: calc(100vh / 2.5); max-height: 250px;\n  background-color: #f2f2f2;\n  border-bottom: 1px solid #dddddd;\n}\n\n.time-series-control {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  background: #f2f2f2;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 48px;\n  border-right: 1px solid #dddddd;\n}\n\n.time-series-number {\n  height: 16px;\n  width: 28px;\n  font-size: 13px;\n  font-weight: 300;\n  border-radius: 50%;\n  padding: 4px;\n  display: flex;\n  line-height: 18px;\n  justify-content: center;\n  color: white;\n}\n\n.time-series-canvas {\n  display: flex;\n  width: 100%; \n  height: calc(100vh / 2.5); max-height: 250px;\n  background: none;\n}\n\n.time-series-amplitude {\n  position: absolute;\n  bottom: 10px;\n  right: 10px;\n  font-size: 12px;\n  color: #f2f2f2;\n  background: #f2f2f2;\n  padding: 3px 6px;\n  border-radius: 9px;\n}\n\n.graph-settings {\n  margin-top: 8px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"